:root {
  --color-white: #fff;
  --color-almost-white: #FCFCFC;
  --color-gray-light-1: #E8E8E8;
  --color-gray-light-2: #949494;
  --color-gray: #353535; }

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

html {
  font-size: 62.5%; }
  @media only screen and (max-width: 50em) {
    html {
      font-size: 60%; } }
  @media only screen and (max-width: 35em) {
    html {
      font-size: 43%; } }

body {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  line-height: 1.6;
  background-image: -webkit-gradient(linear, left top, right bottom, from(var(--color-almost-white)), to(var(--color-gray-light-1)));
  background-image: -webkit-linear-gradient(left top, var(--color-almost-white), var(--color-gray-light-1));
  background-image: -o-linear-gradient(left top, var(--color-almost-white), var(--color-gray-light-1));
  background-image: linear-gradient(to right bottom, var(--color-almost-white), var(--color-gray-light-1));
  background-repeat: no-repeat;
  background-position: top;
  background-size: cover;
  min-height: calc(100vh - 6rem);
  color: var(--color-gray); }
  @media only screen and (max-width: 50em) {
    body {
      min-height: 100%; } }

::-moz-selection {
  background: var(--color-gray-light-1); }

::selection {
  background: var(--color-gray-light-1); }



